import server from '../../server.js';
import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore,
	},
	data() {
		return {
			gaodu:0,
			classList:[],
			home:true,
			TabCur:0,
			inputVal:"",
			postList:[],
			userList:[],
			page:1,
			status: 'loading',
			statusTypes: [{
				value: 'more',
				text: '加载前',
				checked: true
			}, {
				value: 'loading',
				text: '加载中',
				checked: false
			}, {
				value: 'noMore',
				text: '我是有底线的',
				checked: false
			}],
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			weikong:true,
			hotList:[]
		}
	},
	onReady: function() {
		this.getHei()	
	},
	onLoad:function(){
		uni.request({
			method:'GET',
			url: 'https://api.angeli.top/post.php?type=getClassList', //仅为示例，并非真实接口地址。
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				'Cookie':server.cookie
			},
			success: (res) => {
				this.classList=res.data.data;
				console.log(this.classList);
			}
		});
		this.getDayHot();
	},
	onReachBottom:function(){
		this.page++;
		uni.request({
			method:'GET',
			url: 'https://api.angeli.top/post.php?type=searchPosts', //仅为示例，并非真实接口地址。
			data: {
				keyword: this.inputVal,
				count:20,
				page:this.page
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				'Cookie':server.cookie
			},
			success: (res) => {
				console.log("————————————帖子列表——————————"+this.page);
				if(res.data.data.length==undefined){
					this.page--;
					this.status ="noMore";
				}else{
					this.postList=this.postList.concat(res.data.data);
					this.status ="loading";
					this.$forceUpdate()
					console.log(this.postList);
				}
				
			}
		});
		
	},
	methods: {
		sososo:function(event){
			//this.inputVal=event.detail.value;
			this.home=false;
			this.requestSousuo(0)
		},
		getDayHot:function(){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/user.php?type=getDayHot', //仅为示例，并非真实接口地址。
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					this.hotList=res.data.data.data;
					console.log(this.hotList);
				}
			});
		},
		requestSousuo:function(type){
			if(type==0){
				uni.showLoading({
					title: '搜索中..',
					mask:true
				});
				uni.request({
					method:'GET',
					url: 'https://api.angeli.top/post.php?type=searchPosts', //仅为示例，并非真实接口地址。
					data: {
						keyword: this.inputVal,
						count:20,
						page:this.page
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'Cookie':server.cookie
					},
					success: (res) => {
						this.postList=[]
						console.log(res.data.data);
						console.log("————————————帖子列表——————————");
						this.postList=res.data.data;
						console.log(this.postList);
						this.weikong=false
						if(res.data.code!=="1"){
							this.weikong=true
						}
						if(res.data.data==false){
							this.weikong=true
						}
						if(res.data.data.length<21){
							this.status ="noMore";
						}
						this.$forceUpdate();
					},
					complete() {
						uni.hideLoading()
					}
				});
			}else{
				uni.showLoading({
					title: '搜索中..',
					mask:true
				});
				uni.request({
					method:'GET',
					url: 'https://api.angeli.top/user.php?type=search', //仅为示例，并非真实接口地址。
					data: {
						keyword: this.inputVal
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'Cookie':server.cookie
					},
					success: (res) => {
						this.userList=[]
						console.log(res.data.data);
						console.log("————————————搜索用户列表——————————");
						this.userList=res.data.data;
						console.log(this.userList);
						this.weikong=false
						if(res.data.code!=="1"){
							this.weikong=true
						}
						if(res.data.data==false){
							this.weikong=true
						}
						if(res.data.data.length<21){
							this.status ="noMore";
						}
						this.$forceUpdate();
					},
					complete() {
						uni.hideLoading()
					}
				});
			}
			
		},
		getPOST:function(id){
			uni.navigateTo({
				url: '../classPost/classPost?id='+id
			})
		},
		getpostinfo:function(id){
			uni.navigateTo({
				url: '../postinfo/postinfo?id='+id
			});
		},
		getClass:function(id){
			uni.navigateTo({
				url: '../classPost/classPost?id='+id
			})
		},
		Like:function(e){
			uni.showToast({
				title: "给帖子ID为："+e+"点赞",
				position:'bottom',
				icon:'none'
			});
		},
		zan:function(){
			uni.showToast({
				title: "点赞",
				position:'bottom',
				icon:'none'
			});
		},
		caidan:function(){
			uni.showActionSheet({
				itemList: ['分享给朋友', '生成海报', '举报'],
				success: function (res) {
					console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		tabSelect(e) {
			this.TabCur = e
			this.requestSousuo(e)
		},
		input:function(event){
			this.inputVal=event.detail.value;
			this.home=true;
			console.log(this.inputVal)
		},
		getbieren:function(e){
			uni.navigateTo({
				url: '../i/bieren?auid='+e
			})
		},
		getHei(){
			let view = uni.createSelectorQuery().select("#topbox");
			view.fields({
			  size: true,
			  scrollOffset: true
			}, data => {
			  this.gaodu=data.height;
			  console.log("总高度：" + this.gaodu);
			}).exec();
		},
		log:function(e){
			console.log(e)
		},
		showImage(res,c){
			uni.previewImage({
				current:c,
				urls: res,
				longPressActions: {
				itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function (res) {
						
						uni.showToast({
							title: '选中了第' + (res.tapIndex + 1) + '个按钮',
							position:'bottom',
							icon:'none'
						});
						console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
						
					},
					fail: function (res) {
						console.log(res.errMsg);
					}    
				}
			})
		},
		adduser:function(uid,index){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/user.php?type=gzORungz', //仅为示例，并非真实接口地址。
				data: {
					uid: uid,
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					uni.showToast({
						title: res.data.msg,
						position:'bottom',
						icon:'none'
					});
					if(res.data.msg=='关注成功'){
						this.hotList[index].AuId.isGz=true;
					}else{
						this.hotList[index].AuId.isGz=false;
					}
				}
			}); 
		}
	}
}
